body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.modal-body .login-form {
	display: block
}

h1, h2 {
	text-indent: -1px
}

h1, h2, h3, h4, h5, h6 {
	margin: 0 0 .3em;
	color: #242628;
	line-height: 1.15em;
	text-rendering: optimizeLegibility
}

h1 {
	font-size: 2.8rem
}


.gh-signin {
	position: relative
}

.gh-signin {
	margin: 30px auto;
	padding: 20px;
	max-width: 200px;
	width: 100%;
	border: 1px solid #dae1e3;
	background: #f8fbfd;
	border-radius: 5px;
  text-align: center;
}

.gh-signin .form-group {
	margin-bottom: 1.5rem
}

.btn{
  width:80%;
  height: 34px;
  color:black;
  background-color: #242628;
}

.btn1{
	width:10%;
	height: 34px;
	border: 1px solid #dfe1e3;
  }
.gh-signin .btn {
	margin: 0;
	padding: 12px
}

.error .gh-input, .gh-input.error, .gh-select.error {
	border-color: #e25440
}
.gh-input.focus, .gh-input:focus, .gh-select:focus, select:focus {
	outline: 0;
	border-color: #b1b1b1
}

.gh-input, .gh-select, select {
  margin: 20px auto;
	display: block;
	padding: 8px 10px;
	border: 1px solid #dfe1e3;
	border-radius: 4px;
	color: #666;
	font-size: 0.8rem;
	font-weight: 400;
	user-select: text;
	transition: border-color .15s linear;
	-webkit-appearance: none;
  text-align: left
}

.form-group, .gh-input, .gh-select, select {
	-webkit-user-select: text;
	-moz-user-select: text;
	-ms-user-select: text;
	width: 70%
}


#data {
	border-collapse: collapse;
	border: 3px solid #ddd;
	margin: 60px auto;
  }
  
  #data td,
  #data th {
	border: 1px solid #ddd;
	padding: 12px;
  }
  
  #data tr:hover {
	background-color: #c7cef7;
  }
  
  #data th {
	padding: 10px;
	text-align: center;
	background-color: white;
	color: black;
  }

  .buttonundo {
	border: none;
	outline: none;
	font-size: 11px;
	font-family: "Quicksand", sans-serif;
	color: #f44336;
	padding: 3px 10px;
	border-radius: 8px;
	cursor: pointer;
	border: 1px solid black;
	background-color: lightgrey;
  }
  
  .buttonundo:active {
	border: 1px solid blue;
  }